import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import {
  setSortState,
  sortSelector,
  setIndexState,
  indexSelector,
} from '../redux/slices/filterSlice';

//Типы:
type SortType = {
  title: string;
  sort: string;
};

export const popupMenu: SortType[] = [
  { title: 'Популярности', sort: 'rating' },
  { title: 'Цене', sort: 'price' },
  { title: 'Алфавиту', sort: 'alphabet' },
];

const Sort: React.FC = React.memo(() => {
  const [popup, setPopup] = React.useState(false);
  const indexSort = useSelector(indexSelector);
  const activeSort = useSelector(sortSelector);
  const dispatch = useDispatch();
  const sortRef = React.useRef<HTMLDivElement>(null);

  const toggle = (elem: SortType, index: number) => {
    dispatch(setSortState(elem.sort));
    dispatch(setIndexState(index));
    setPopup(false);
  };

  React.useEffect(() => {
    const outsideClick = (event: MouseEvent) => {
      const _event = event as MouseEvent & {
        path: Node[];
      };
      if (!_event.path.includes(sortRef.current!)) {
        setPopup(false);
      }
    };
    document.body.addEventListener('click', outsideClick);

    return () => document.body.removeEventListener('click', outsideClick);
  }, []);

  const activePopup = popupMenu.map((elem, index) => (
    <li
      key={index}
      onClick={() => toggle(elem, index)}
      className={activeSort === elem.sort ? 'active' : ''}>
      {elem.title}
    </li>
  ));

  return (
    <div ref={sortRef} className="sort">
      <div className="sort__label">
        <svg
          width="10"
          height="6"
          viewBox="0 0 10 6"
          fill="none"
          xmlns="http://www.w3.org/2000/svg">
          <path
            d="M10 5C10 5.16927 9.93815 5.31576 9.81445 5.43945C9.69075 5.56315 9.54427 5.625 9.375 5.625H0.625C0.455729 5.625 0.309245 5.56315 0.185547 5.43945C0.061849 5.31576 0 5.16927 0 5C0 4.83073 0.061849 4.68424 0.185547 4.56055L4.56055 0.185547C4.68424 0.061849 4.83073 0 5 0C5.16927 0 5.31576 0.061849 5.43945 0.185547L9.81445 4.56055C9.93815 4.68424 10 4.83073 10 5Z"
            fill="#2C2C2C"
          />
        </svg>
        <b>Сортировка по:</b>
        <span onClick={() => setPopup(!popup)}>{popupMenu[indexSort].title}</span>
      </div>
      {popup && (
        <div className="sort__popup">
          <ul>{activePopup}</ul>
        </div>
      )}
    </div>
  );
});

export default Sort;
